import { useState, useEffect, useMemo } from 'react'
import { View, Text } from '@tarojs/components'
import Taro, { useRouter } from '@tarojs/taro'
import TherapistHeader from '../../../components/TherapistHeader'
import SymptomCategoryTabs from '../../../components/SymptomCategoryTabs'
import SymptomServiceList from '../../../components/SymptomServiceList'
import ShoppingCart from '../therapist/components/ShoppingCart'
import { symptomService } from '../../../services/symptom'
import { therapistService } from '../../../services/therapist'
import './index.scss'

interface CartItem {
  serviceId: string
  serviceName: string
  duration: number
  price: number
  discountPrice?: number
  date: string
  time: string
  therapistName: string
  therapistAvatar?: string
}

const SymptomPage = () => {
  const router = useRouter()
  const { therapistId, therapistName, storeId, storeName } = router.params

  const [therapist, setTherapist] = useState<any>(null)
  const [categories, setCategories] = useState<any[]>([])
  const [services, setServices] = useState<any[]>([])
  const [activeCategoryId, setActiveCategoryId] = useState('')
  const [cartItems, setCartItems] = useState<CartItem[]>([])
  const [loading, setLoading] = useState(true)

  // Ëé∑ÂèñÊé®ÊãøÂ∏à‰ø°ÊÅØ
  useEffect(() => {
    if (therapistId) {
      therapistService.getTherapistDetail(therapistId as string).then(res => {
        setTherapist(res.data)
      })
    }
  }, [therapistId])

  // Ëé∑ÂèñÁóáÁä∂ÂàÜÁ±ª
  useEffect(() => {
    symptomService.getCategories().then(res => {
      setCategories(res.data)
      if (res.data.length > 0) {
        setActiveCategoryId(res.data[0].id)
      }
    })
  }, [])

  // Ëé∑ÂèñÊúçÂä°ÂàóË°®
  useEffect(() => {
    if (therapistId) {
      setLoading(true)
      symptomService.getTherapistSymptomServices(therapistId as string).then(res => {
        setServices(res.data)
        setLoading(false)
      })
    }
  }, [therapistId])

  // Ê†πÊçÆÂàÜÁ±ªÁ≠õÈÄâÊúçÂä°
  const filteredServices = useMemo(() => {
    return services.filter(service => service.categoryId === activeCategoryId)
  }, [services, activeCategoryId])

  // Ë¥≠Áâ©ËΩ¶‰∏≠ÁöÑÊúçÂä°IDÂàóË°®
  const cartServiceIds = useMemo(() => {
    return cartItems.map(item => item.serviceId)
  }, [cartItems])

  // Ê∑ªÂä†Âà∞Ë¥≠Áâ©ËΩ¶
  const handleAddToCart = (service: any) => {
    if (!therapist) return

    const newItem: CartItem = {
      serviceId: service.id,
      serviceName: service.name,
      duration: service.duration,
      price: service.price,
      discountPrice: service.discountPrice,
      date: new Date().toISOString(),
      time: 'ÂæÖÈÄâÊã©',
      therapistName: therapist.name,
      therapistAvatar: therapist.avatar
    }

    setCartItems([...cartItems, newItem])

    Taro.showToast({
      title: 'Â∑≤Ê∑ªÂä†Âà∞Ë¥≠Áâ©ËΩ¶',
      icon: 'none'
    })
  }

  // Êü•ÁúãÊé®ÊãøÂ∏àËØ¶ÊÉÖ
  const handleViewDetail = () => {
    Taro.navigateTo({
      url: `/pages/appointment/therapist/index?id=${therapistId}&storeId=${storeId}&storeName=${storeName}`
    })
  }

  // ÂéªÁªìÁÆó
  const handleCheckout = () => {
    if (cartItems.length === 0) {
      Taro.showToast({
        title: 'ËØ∑ÂÖàÈÄâÊã©ÊúçÂä°È°πÁõÆ',
        icon: 'none'
      })
      return
    }

    const params = {
      items: JSON.stringify(cartItems),
      therapistId,
      therapistName,
      storeId,
      storeName,
      from: 'symptom'
    }

    Taro.navigateTo({
      url: `/pages/booking/confirm/index?${Object.entries(params)
        .map(([key, value]) => `${key}=${encodeURIComponent(value)}`)
        .join('&')}`
    })
  }

  if (!therapist) {
    return <View className="symptom-page loading">Âä†ËΩΩ‰∏≠...</View>
  }

  return (
    <View className="symptom-page">
      {/* Êé®ÊãøÂ∏à‰ø°ÊÅØÂ§¥ÈÉ® */}
      <TherapistHeader
        therapist={{
          id: therapist.id,
          name: therapist.name,
          avatar: therapist.avatar,
          level: therapist.level || 3,
          rating: therapist.rating
        }}
        onDetailClick={handleViewDetail}
      />

      {/* ‰∏ªÂÜÖÂÆπÂå∫ */}
      <View className="symptom-content">
        {/* Â∑¶‰æßÂàÜÁ±ªÊ†áÁ≠æ */}
        <SymptomCategoryTabs
          categories={categories}
          activeId={activeCategoryId}
          onChange={setActiveCategoryId}
        />

        {/* Âè≥‰æßÊúçÂä°ÂàóË°® */}
        <SymptomServiceList
          services={filteredServices}
          onAddToCart={handleAddToCart}
          cartServiceIds={cartServiceIds}
        />
      </View>

      {/* Â∫ïÈÉ®Ë¥≠Áâ©ËΩ¶ */}
      <ShoppingCart
        items={cartItems}
        therapist={therapist}
        onCheckout={handleCheckout}
      />
    </View>
  )
}

export default SymptomPage