// Ëé∑ÂèñÊé®ÊãøÂ∏àÊîØÊåÅÁöÑÁóáÁä∂ÊúçÂä°
export const getTherapistSymptomServices = (therapistId: string) => {
  // Ê®°Êãü‰∏çÂêåÊé®ÊãøÂ∏àÊîØÊåÅ‰∏çÂêåÁöÑÊúçÂä°È°πÁõÆ
  // ËøôÈáåÁÆÄÂåñÂ§ÑÁêÜÔºåËøîÂõûÊâÄÊúâÊúçÂä°
  return symptomServices.map(service => ({
    ...service,
    therapistId,
    // Ê®°ÊãüÂä®ÊÄÅÂèØÁî®ÊÄß
    availability: Math.random() > 0.7 ? 'busy' : service.availability
  }))
}